
.review {
  display: flex;
  width: 168px;
  align-items: center;
  margin: 0 auto;

  &__star {
    width: 16px;
    height: 16px;
    background-image: url(/images/star.svg);
    background-repeat: no-repeat;
    margin-right: 4px;
  }
  
  &--stars-1 > :nth-child(1),
  &--stars-2 > :nth-child(-n+2),
  &--stars-3 > :nth-child(-n+3),
  &--stars-4 > :nth-child(-n+4),
  &--stars-5 > :nth-child(-n+5) {
    background-image: url(/images/star-active.svg);
  }

  &__text {
    margin: 0 0 0 15px;
    font-size: 10px;
    line-height: 14px;
  }

}
